body {
  background-color: #f4f4f4;
  margin: 20px;
  font-family: sans-serif;
}

.app-container {
  background-color: #fff;
  border-radius: 8px;
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
  box-shadow: 0 0 10px #0000001a;
}

header h1 {
  text-align: center;
  color: #333;
}

section {
  border: 1px solid #eee;
  border-radius: 5px;
  margin-bottom: 30px;
  padding: 15px;
}

section h2 {
  color: #555;
  border-bottom: 1px solid #eee;
  margin-top: 0;
  padding-bottom: 10px;
}

form {
  margin-bottom: 20px;
}

form input, form select, form button {
  border: 1px solid #ddd;
  border-radius: 4px;
  margin-right: 10px;
  padding: 8px;
}

form button {
  color: #fff;
  cursor: pointer;
  background-color: #5cb85c;
}

form button:hover {
  background-color: #4cae4c;
}

.error-message {
  color: #721c24;
  background-color: #f8d7da;
  border: 1px solid #f5c6cb;
  border-radius: 4px;
  margin-bottom: 20px;
  padding: 10px;
}

.task-columns-container {
  justify-content: space-between;
  gap: 20px;
  display: flex;
}

.task-column {
  background-color: #e9ecef;
  border-radius: 5px;
  flex: 1;
  min-width: 250px;
  padding: 15px;
}

.task-column h3 {
  color: #333;
  border-bottom: 1px solid #ccc;
  margin-top: 0;
  padding-bottom: 5px;
}

.task-card {
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 4px;
  margin-bottom: 10px;
  padding: 10px;
  box-shadow: 0 1px 3px #0000001a;
}

.task-card h4 {
  margin-top: 0;
  margin-bottom: 5px;
}

.task-card p {
  color: #555;
  margin: 4px 0;
  font-size: .9em;
}

.task-actions {
  margin-top: 10px;
}

.task-actions select, .task-actions button {
  margin-top: 5px;
  font-size: .9em;
}

.task-actions .delete-button {
  background-color: #d9534f;
}

.task-actions .delete-button:hover {
  background-color: #c9302c;
}

.filter-sort-controls {
  background-color: #f9f9f9;
  border: 1px solid #eee;
  margin-bottom: 20px;
  padding: 10px;
}

.filter-sort-controls div {
  margin-bottom: 8px;
}

.filter-sort-controls label {
  margin-right: 5px;
  font-weight: bold;
}

.member-list ul {
  padding: 0;
  list-style: none;
}

.member-list li {
  border-bottom: 1px dotted #eee;
  padding: 5px;
}
